<template>
	<app-scroll-box class="page-clientsManage">
		<el-row class="pa-20">
			<div class="permission">
				<div>
					
				</div>
				<div class="permission-table" v-for="(item, idx) in sidebar" :key="idx">
					<div v-if="item.children">
						<div class="table-body" v-for="(childrenItem, idx2) in item.children" :key="idx2">
							<div class="first-col">
								<span v-if="idx2 === 0" style="display: inline-block; line-height: 30px; cursor: pointer; ">
									<el-checkbox :label="item.title" v-model="item.checked" :key="item.index" @change="handleChecked('firsh', item)">{{ item.title }}</el-checkbox>
								</span>
							</div>
							<div class="width120">
								<span style="display: inline-block; line-height: 30px; cursor: pointer; ">
									<el-checkbox :label="childrenItem.title" v-model="childrenItem.checked" :key="childrenItem.index" @change="handleChecked('children', item)">{{ childrenItem.title }}</el-checkbox>
								</span>
							</div>
							<div class="width265">
								<el-checkbox v-for="threeItem in childrenItem.children" v-model="threeItem.checked" :label="threeItem.title" :key="threeItem.index" @change="handleChecked('threeChildren', item)">{{threeItem.title}}</el-checkbox>
							</div>
						</div>
					</div>
					<div class="table-body" v-else>
						<span class="first-col" style="display: inline-block; line-height: 30px; cursor: pointer; ">
							<el-checkbox @change="handleChecked('firsh', item)" :label="item.title" v-model="item.checked" :key="item.index">{{ item.title }}</el-checkbox>
						</span>
					</div>
				</div>
			</div>
			</el-card>
		</el-row>
	</app-scroll-box>
</template>
<script>
	import sidebar from '@/components/sidebar.js'
	export default {
		name: "deliverySetting",
		components: {},
		props: {},
		data() {
			return {
				itemTitle: "",
				itemId: 0,
				sidebar: sidebar,
				countries: [{
						cid: 1,
						cname: "中国",
						pid: "1",
						pname: "广东",
						cityId: 1,
						cityName: "深圳"
					},
					{
						cid: 1,
						cname: "中国",
						pid: "1",
						pname: "广东",
						cityId: 2,
						cityName: "肇庆"
					},
					{
						cid: 1,
						cname: "中国",
						pid: "2",
						pname: "湖北",
						cityId: 3,
						cityName: "武汉"
					},
					{
						cid: 1,
						cname: "中国",
						pid: "2",
						pname: "湖北",
						cityId: 4,
						cityName: "咸宁"
					},
					{
						cid: 1,
						cname: "中国",
						pid: "2",
						pname: "湖北",
						cityId: 5,
						cityName: "恩施"
					},
					{
						cid: 2,
						cname: "美国",
						pid: "3",
						pname: "usa省州1",
						cityId: 6,
						cityName: "usa-city-1"
					},
					{
						cid: 2,
						cname: "美国",
						pid: "3",
						pname: "usa省州1",
						cityId: 7,
						cityName: "usa-city-2"
					},
					{
						cid: 2,
						cname: "美国",
						pid: "3",
						pname: "usa省州1",
						cityId: 8,
						cityName: "usa-city-3"
					},
					{
						cid: 2,
						cname: "美国",
						pid: "4",
						pname: "usa省州2",
						cityId: 9,
						cityName: "usa-city-4"
					},
					{
						cid: 2,
						cname: "美国",
						pid: "4",
						pname: "usa省州2",
						cityId: 10,
						cityName: "usa-city-5"
					}
				],
				checkedCities: [1, 4, 9, 10]
			};
		},
		computed: {},
		methods: {
			//操作数据
			opreationData() {
				this.countries.forEach(c => {
					c.pros.forEach(p => {
						if (p.cities && p.cities.length > 0) {
							p.checked = p.cities.every(p => p.checked)
						}
					})

					if (c.pros && c.pros.length > 0) {
						c.checked = c.pros.every(p => p.checked)
					}
				})
			},
			handleChecked(level, item) {
				let isChecked = item.checked
				switch (level) {
					case "country":
						if (item && item.pros) {
							item.pros.forEach(p => {
								if (p.cities) {
									p.cities.forEach(c => {
										c.checked = isChecked
									})
								}
							})
						}
						break;
					case "pro":
						if (item && item.cities) {
							item.cities.forEach(c => {
								c.checked = isChecked
							})
						}
						break;
					case "city":
						break;
				}
				this.opreationData()
			}
		},
		created: function() {},
		mounted() {
			this.initPage();
		},
		watch: {}
	};
</script>
